export default {
  name: 'TableMixin',
  data() {
    return {
      // loading
      loading: false,
      // 
      pageIndex: 1,
      pageSize: 20,
      total: 200
    }
  },

  computed: {
    currentPage: {
      get() {
        return this.pageIndex
      },
      set(n) {
        this.pageIndex = n
      }
    }
  },

  methods: {
    /**
     * 
     */
    pageSizeChange(param) {
      this.pageSize = param
      this.pageIndex = 1
      this.getTableList()
    },
    /**
     * 
     */
    pageChange(e) {
      this.pageIndex = e
      this.getTableList()
    },
    /**
     * mixintable
     */
    getTableList() {}
  }
}
